import { useRouter } from 'next/router';
import { useEffect, useState } from 'react';

import { Typography } from 'src/components/dataDisplay';
import { PreventLargeWidthBox } from 'src/components/layout';
import {
  BestFitProductLodingPaper,
  BestFitProductResultPaper,
} from 'src/components/papers';
import { BASE_PATH } from 'src/constants/resouce';

const removeFirstSlash = (path: string) => {
  if (!path || !path.length) {
    return path;
  }
  const first = path.substring(0, 1);
  if (first !== '/') {
    return path;
  }
  return path.substring(1);
};

const BestFitProductResult = (): JSX.Element => {
  const [isLoading, setIsLoading] = useState(true);
  const router = useRouter();
  const { pathname = '', basePath = '', query } = router;
  const productId = query.id;

  // TODO: 에러 페이지는 따로 정리해야 한다.
  const goBestFitProductHome = () => {
    const pathnames = pathname.split('/');
    pathnames.pop(); // Remove the last element
    const parentPathname = removeFirstSlash(pathnames.join('/'));
    router.push(`${BASE_PATH}${basePath}${parentPathname}`);
  };

  useEffect(() => {
    setTimeout(() => setIsLoading(false), 3000);
  }, []);

  if (!productId) {
    return (
      <PreventLargeWidthBox>
        <div>
          <Typography variant="body">올바르지 않은 접근 입니다.</Typography>
        </div>
        <button type="button" onClick={goBestFitProductHome}>
          내게 맞는 제품 홈
        </button>
      </PreventLargeWidthBox>
    );
  }

  if (isLoading) {
    return <BestFitProductLodingPaper />;
  }

  return (
    <PreventLargeWidthBox>
      <BestFitProductResultPaper />
    </PreventLargeWidthBox>
  );
};

export default BestFitProductResult;
